.st-selectable-grid-container {
  width: 100%;
  overflow: hidden;
  padding: 0.5rem 0;
  color: var(--text-color);
  font-family: var(--font);
  box-sizing: border-box;
  text-align: center;
}

.st-grid-table {
  border-collapse: separate;
  border-spacing: 0;
  background-color: transparent;
  table-layout: fixed; 
  max-width: 100%;
  margin: 0 auto;
}

/* Base cell styles */
.st-grid-cell {
  border: 1px solid;
  border-color: rgba(0, 0, 0, 0.6);
  padding: 0;
  text-align: center;
  cursor: pointer;
  position: relative;
  box-sizing: border-box;
  font-weight: normal;
  border-radius: 0;
  overflow: hidden;
  vertical-align: middle; 
}

/* Cell content container */
.cell-content {
  position: relative;
  width: 100%;
  height: 0;
}

/* Cell label - centered within the cell */
.cell-label {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem;
  overflow: hidden;
}

/* Base styles for headers and index */
.st-grid-header, .st-grid-index {
  padding: 0.35rem 0.25rem;
  position: relative;
  background-color: transparent;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
}

/* Headers style - NO BORDERS */
.st-grid-header {
  font-weight: bold;
  border: none;
  min-width: 50px;
  height: 38px;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.st-grid-header[style*="background-color"] {
  height: 36px; 
  border-bottom: none !important;
  transform: translateY(-4px); /* Move it up slightly */
  border-left: 1px solid rgba(0, 0, 0, 0.5);
  border-right: 1px solid rgba(0, 0, 0, 0.5);
}

/* Index style */
.st-grid-index {
  font-weight: bold;
  border: none;
  min-width: 70px;
  text-align: right;
  padding-right: 6px;
}

/* Make the corner cell transparent */
.st-grid-corner {
  background-color: transparent;
  border: none;
  box-sizing: border-box;
}

/* Primary selection styles */
.primary-selected {
  box-shadow: inset 0 0 0 2px #2196F3;
  z-index: 1;
}

/* Primary selection overlay */
.primary-selected::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2196F3;
  opacity: 0.1;
  pointer-events: none;
  border-radius: 3px;
}

/* Secondary selection styles */
.secondary-selected {
  box-shadow: inset 0 0 0 2px #FF9800;
  z-index: 1;
}

/* Secondary selection overlay */
.secondary-selected::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #FF9800;
  opacity: 0.1;
  pointer-events: none;
  border-radius: 3px;
}

/* Hover effect */
.st-grid-cell:hover:not(.primary-selected):not(.secondary-selected)::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.05);
  pointer-events: none;
}

/* Dark theme hover adjustment */
[data-theme="dark"] .st-grid-cell:hover:not(.primary-selected):not(.secondary-selected)::after {
  background-color: rgba(255, 255, 255, 0.05);
}

/* Apply custom background color only for headers with explicit background-color */
.st-grid-header[style*="background-color"] {
  transition: background-color 0.2s ease;
}

/* Focus outlines for accessibility */
.st-grid-cell:focus, .st-grid-header:focus {
  outline: 2px solid rgba(66, 133, 244, 0.6);
  outline-offset: -2px;
}

/* Add more spacing between headers and cells */
thead tr {
  margin-bottom: 8px;
}

/* Fixed height container styling */
.st-selectable-grid-container {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.08) transparent;
}

[data-theme="dark"] .st-selectable-grid-container {
  scrollbar-color: rgba(255, 255, 255, 0.08) transparent;
}

/* Text-fitting container with better text handling */
.text-fit-container {
  display: block;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: clip;
  max-width: 100%;
  width: 100%;
  font-size: 1rem;
  line-height: 1.2;
  text-align: center;
}

/* For header cells - ensure consistent font size across row */
thead .text-fit-container {
  text-align: center;
  font-weight: bold;
  /* Will be set dynamically in JS */
}

/* For index cells */
.st-grid-index .text-fit-container {
  text-align: right;
  padding-right: 4px;
}

/* For responsive behavior */
@media (max-width: 768px) {
  .st-grid-cell {
    min-width: 30px;
  }
  
  .st-grid-header, .st-grid-index {
    min-width: 30px;
  }
}

/* Style for cells with small width */
td.st-grid-cell[style*="width:"] .text-fit-container {
  max-width: 100%;
}

/* Container with height constraints */
.st-selectable-grid-container[style*="height:"] {
  overflow: auto; /* Only show scrollbars when needed */
}

/* Non-resizing grid styling */
.st-grid-table[style*="display: inline-block"] {
  width: auto !important;
}

/* Grid positioning styles */
.st-selectable-grid-container[style*="text-align: left"] .st-grid-table {
  margin-right: auto;
}

.st-selectable-grid-container[style*="text-align: center"] .st-grid-table {
  margin-left: auto;
  margin-right: auto;
}

.st-selectable-grid-container[style*="text-align: right"] .st-grid-table {
  margin-left: auto;
}

/* Set Tooltip over cells */
.st-grid-cell[title],
.st-grid-header[title],
.st-grid-index[title] {
  position: relative;
  cursor: pointer;
}

/* Cell marker - dot in the top right corner */
.cell-marker {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #2196F3;
  z-index: 1;
}

/* do not show annoying border when box is focused */
.st-selectable-grid-container:focus {
  outline: none;
}
